<template>
    <p-data-table
        :value="reportList"
        v-model:selection="selectedReport"
        selectionMode="single"
        dataKey="autoTestOrderId"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="loading"
        :paginator="true"
        :rows="20"
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entries"
        :rowsPerPageOptions="[10, 20, 50]"
    >
        <p-column header="" field="checked">
            <template #body="{data}">
                <p-checkbox v-model="data.checked" :binary="true" />
            </template>
        </p-column>
        <p-column header="#" field="testOrderId" />
        <p-column header="Last Name" field="lastName" />
        <p-column header="First Name" field="firstName" />
        <p-column header="Birth Date" field="birthDate" />
        <p-column header="Cycle" field="cycleType" />
        <p-column header="Date" field="orderDate" />
        <p-column header="E2" field="etwo" />
        <p-column header="LH" field="lh" />
        <p-column header="PROG" field="prog" />
        <p-column header="FSH" field="fsh" />
        <p-column header="TSH" field="tsh" />
        <p-column header="PRL" field="prl" />
        <p-column header="BhCG" field="bhcg" />
        <p-column header="TESTO" field="testo" />
        <p-column header="DHEAS" field="dheas" />
        <p-column header="AMHP" field="amhp" />
        <p-column header="!!" field="checked2">
            <template #body="{data}">
                <p-checkbox v-model="data.checked2" :binary="true" />
            </template>
        </p-column>
        <p-column header="Doctor" field="doctorName" />
        <p-column header="Loc" field="location" />
        <p-column header="Modified" field="lastModifiedUser" />
        <p-column header="Modified !!" field="lastModifiedUser2" />
    </p-data-table>
</template>

<script>
import { computed, inject, ref } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const reportList = computed(() => store.state.dailyReport.reportList);
        const selectedReport = computed({
            get: () => store.state.dailyReport.selectedReport,
            set: (val) => {
                store.commit("dailyReport/SET_SELECTED_REPORT", val);
            }
        });
        const loading = inject("loading");

        return {
            reportList,
            loading,
            selectedReport
        };
    }
};
</script>
